import React from 'react';
import { useDispatch } from 'react-redux';
import { clearAlert, clearAll } from '../../y_redux/modules/modalSlice';
import '../../y_css/confirmRemove.css';


const ConfirmRemove = () => {
  const dispatch = useDispatch();
  const removeAll = () => {
    dispatch(clearAll());
  }
  const goBack = (e) => {
    const {target:{className}} = e;
    if(className === 'modal_background' || className === "confirm_btn_cancel"){
      dispatch(clearAlert());
    }
  };

  return(
    <div className="modal_background" onClick={goBack}>
      <div className="modal_board3">
        <div className="confirm_remove_note">
          <div className="confirm_remove_note_top"> ?</div>
          <div className="confirm_remove_note_bottom">     .</div>
        </div>
        <button className="confirm_btn_remove" onClick={removeAll}></button>
        <button className="confirm_btn_cancel" onClick={goBack}></button>
      </div>
    </div>
  )
}

export default ConfirmRemove;